.shooting-stars {
    z-index: 1;
    width: 5px;
    height: 85px;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    position: absolute;
    bottom: 0;
    right: 0;
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(255, 255, 255, 0)), to(white));
    background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0), white);
    background: -o-linear-gradient(bottom, rgba(255, 255, 255, 0), white);
    background: linear-gradient(to top, rgba(255, 255, 255, 0), white);
    -webkit-animation: animShootingStar 10s linear infinite;
            animation: animShootingStar 10s linear infinite;
}

@-webkit-keyframes animStar {
    from {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    to {
        -webkit-transform: translateY(-2560px) translateX(-2560px);
                transform: translateY(-2560px) translateX(-2560px);
    }
}

@keyframes animStar {
    from {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    to {
        -webkit-transform: translateY(-2560px) translateX(-2560px);
                transform: translateY(-2560px) translateX(-2560px);
    }
}
@-webkit-keyframes animShootingStar {
    from {
        -webkit-transform: translateY(0px) translateX(0px) rotate(-45deg);
                transform: translateY(0px) translateX(0px) rotate(-45deg);
        opacity: 1;
        height: 5px;
    }
    to {
        -webkit-transform: translateY(-2560px) translateX(-2560px) rotate(-45deg);
                transform: translateY(-2560px) translateX(-2560px) rotate(-45deg);
        opacity: 1;
        height: 800px;
    }
}
@keyframes animShootingStar {
    from {
        -webkit-transform: translateY(0px) translateX(0px) rotate(-45deg);
                transform: translateY(0px) translateX(0px) rotate(-45deg);
        opacity: 1;
        height: 5px;
    }
    to {
        -webkit-transform: translateY(-2560px) translateX(-2560px) rotate(-45deg);
                transform: translateY(-2560px) translateX(-2560px) rotate(-45deg);
        opacity: 1;
        height: 800px;
    }
}



